.theme-toggle {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  border-radius: 30px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 30px;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.toggle-track {
  width: 100%;
  height: 100%;
  border-radius: 30px;
  position: relative;
  transition: all 0.3s ease;
}

.toggle-thumb {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  position: absolute;
  top: 3px;
  left: 3px;
  z-index: 2;
}

.toggle-thumb svg {
  width: 16px;
  height: 16px;
  transition: all 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .toggle-track {
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  box-shadow: 
    0 0 20px rgba(0, 212, 255, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

[data-theme="dark"] .toggle-thumb {
  background: rgba(255, 255, 255, 0.9);
  color: #00d4ff;
  transform: translateX(0);
}

[data-theme="dark"] .toggle-thumb svg {
  color: #00d4ff;
}

/* Светлая тема */
[data-theme="light"] .toggle-track {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  box-shadow: 
    0 0 20px rgba(102, 126, 234, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

[data-theme="light"] .toggle-thumb {
  background: rgba(255, 255, 255, 0.9);
  color: #667eea;
  transform: translateX(33px);
}

[data-theme="light"] .toggle-thumb svg {
  color: #667eea;
}

/* Hover эффекты */
.theme-toggle:hover .toggle-track {
  transform: scale(1.05);
}

.theme-toggle:hover .toggle-thumb {
  transform: scale(1.1);
}

[data-theme="light"] .theme-toggle:hover .toggle-thumb {
  transform: translateX(33px) scale(1.1);
}

/* Мобильная адаптивность */
@media (max-width: 480px) {
  .theme-toggle {
    width: 50px;
    height: 25px;
  }

  .toggle-thumb {
    width: 20px;
    height: 20px;
  }

  .toggle-thumb svg {
    width: 14px;
    height: 14px;
  }

  [data-theme="light"] .toggle-thumb {
    transform: translateX(30px);
  }

  [data-theme="light"] .theme-toggle:hover .toggle-thumb {
    transform: translateX(30px) scale(1.1);
  }
} 