.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  backdrop-filter: blur(10px);
  animation: fadeIn 0.3s ease;
}

.modal-content {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 40px;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  border: 1px solid rgba(0, 212, 255, 0.2);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .modal-content {
  background: rgba(26, 26, 46, 0.95);
  border: 1px solid rgba(0, 212, 255, 0.3);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(0, 212, 255, 0.1);
}

.modal-close {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: #666;
  transition: all 0.3s ease;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.modal-close:hover {
  background: rgba(0, 0, 0, 0.1);
  color: #333;
}

/* Темная тема */
[data-theme="dark"] .modal-close {
  color: #a0aec0;
}

[data-theme="dark"] .modal-close:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #e2e8f0;
}

.modal-content h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
  text-align: center;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.modal-content p {
  text-align: center;
  margin-bottom: 30px;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .modal-content p {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .modal-content p {
  color: #4a5568;
}

.order-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-weight: 600;
  font-size: 1rem;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .form-group label {
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .form-group label {
  color: #1a202c;
}

.form-group input,
.form-group select {
  padding: 15px;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 16px;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.9);
  color: #2d3748;
}

/* Темная тема */
[data-theme="dark"] .form-group input,
[data-theme="dark"] .form-group select {
  background: rgba(10, 10, 10, 0.8);
  border-color: rgba(0, 212, 255, 0.3);
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .form-group input,
[data-theme="light"] .form-group select {
  background: rgba(255, 255, 255, 0.9);
  border-color: rgba(0, 212, 255, 0.2);
  color: #1a202c;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #00d4ff;
  box-shadow: 0 0 0 3px rgba(0, 212, 255, 0.1);
}

.submit-btn {
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
  box-shadow: 0 10px 20px rgba(0, 212, 255, 0.3);
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 15px 30px rgba(0, 212, 255, 0.4);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.success-message {
  text-align: center;
  padding: 20px;
}

.success-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  animation: bounce 0.6s ease;
}

.success-message h3 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #00d4ff;
}

.success-message p {
  font-size: 1.1rem;
  color: #4a5568;
}

/* Темная тема */
[data-theme="dark"] .success-message p {
  color: #a0aec0;
}

/* Анимации */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Мобильная адаптивность */
@media (max-width: 768px) {
  .modal-content {
    padding: 30px 20px;
    width: 95%;
  }

  .modal-content h2 {
    font-size: 1.6rem;
  }

  .modal-content p {
    font-size: 1rem;
  }

  .form-group input,
  .form-group select {
    padding: 12px;
    font-size: 16px;
  }

  .submit-btn {
    padding: 12px 25px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .modal-content {
    padding: 25px 15px;
  }

  .modal-content h2 {
    font-size: 1.4rem;
  }

  .success-icon {
    font-size: 3rem;
  }

  .success-message h3 {
    font-size: 1.5rem;
  }
} 