.footer {
  color: white;
  padding: 60px 0 20px;
  transition: all 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .footer {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 100%);
  border-top: 1px solid rgba(0, 212, 255, 0.2);
}

/* Светлая тема */
[data-theme="light"] .footer {
  background: linear-gradient(135deg, #2d3748 0%, #4a5568 100%);
  border-top: 1px solid rgba(0, 212, 255, 0.2);
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  margin-bottom: 40px;
}

.footer-section h4 {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 20px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .footer-section h4 {
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .footer-section h4 {
  color: #f7fafc;
}

.footer-logo h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.footer-logo p {
  margin-bottom: 20px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .footer-logo p {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .footer-logo p {
  color: #e2e8f0;
}

.footer-social {
  display: flex;
  gap: 15px;
}

.social-link {
  display: inline-block;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 212, 255, 0.3);
}

.footer-links {
  list-style: none;
  padding: 0;
}

.footer-links li {
  margin-bottom: 10px;
}

.footer-links a {
  text-decoration: none;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .footer-links a {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .footer-links a {
  color: #e2e8f0;
}

.footer-links a:hover {
  color: #00d4ff;
}

.footer-contact p {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .footer-contact p {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .footer-contact p {
  color: #e2e8f0;
}

.footer-bottom {
  border-top: 1px solid #4a5568;
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.footer-info p {
  font-size: 0.9rem;
  margin-bottom: 5px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .footer-info p {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .footer-info p {
  color: #e2e8f0;
}

.scroll-to-top {
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
}

.scroll-to-top:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(0, 212, 255, 0.4);
}

@media (max-width: 768px) {
  .footer {
    padding: 40px 0 20px;
  }

  .footer-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .footer-bottom {
    flex-direction: column;
    text-align: center;
  }
} 