.services {
  transition: background 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .services {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 100%);
}

/* Светлая тема */
[data-theme="light"] .services {
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 60px;
}

.service-card {
  border-radius: 20px;
  padding: 30px;
  border: 1px solid rgba(0, 212, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

/* Темная тема */
[data-theme="dark"] .service-card {
  background: rgba(26, 26, 46, 0.8);
  box-shadow: 
    0 10px 30px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(0, 212, 255, 0.1);
}

/* Светлая тема */
[data-theme="light"] .service-card {
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 
    0 10px 30px rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(0, 212, 255, 0.1);
}

.service-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(0, 212, 255, 0.3),
    0 0 30px rgba(0, 212, 255, 0.2);
}

.service-icon {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  font-size: 2.5rem;
  box-shadow: 0 10px 20px rgba(0, 212, 255, 0.3);
}

.service-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 15px;
  transition: color 0.3s ease;
}

.service-description {
  margin-bottom: 20px;
  line-height: 1.6;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .service-title {
  color: #e2e8f0;
}

[data-theme="dark"] .service-description {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .service-title {
  color: #1a202c;
}

[data-theme="light"] .service-description {
  color: #4a5568;
}

.service-features {
  list-style: none;
  margin-bottom: 25px;
}

.service-features li {
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .service-features li {
  color: #cbd5e0;
}

/* Светлая тема */
[data-theme="light"] .service-features li {
  color: #2d3748;
}

.service-features li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #00d4ff;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

.service-price {
  margin-bottom: 20px;
}

.price {
  font-size: 1.3rem;
  font-weight: 700;
  color: #00d4ff;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.service-btn {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  font-weight: 600;
}

@media (max-width: 768px) {
  .services-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .service-card {
    padding: 25px;
  }

  .service-icon {
    width: 60px;
    height: 60px;
    font-size: 2rem;
  }

  .service-title {
    font-size: 1.3rem;
  }
} 