.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
  transition: background 0.3s ease;
}

.theme-toggle-container {
  position: absolute;
  top: 100px;
  right: 30px;
  z-index: 10;
}

/* Темная тема */
[data-theme="dark"] .hero {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
}

/* Светлая тема */
[data-theme="light"] .hero {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(0, 212, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 107, 107, 0.1) 0%, transparent 50%),
    url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="grid" width="50" height="50" patternUnits="userSpaceOnUse"><path d="M 50 0 L 0 0 0 50" fill="none" stroke="rgba(0,212,255,0.1)" stroke-width="1"/></pattern></defs><rect width="100%" height="100%" fill="url(%23grid)"/></svg>');
  opacity: 0.5;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}

.hero-content {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
  padding: 100px 0;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 20px;
  line-height: 1.2;
}

.highlight {
  background: linear-gradient(45deg, #00d4ff, #ff6b6b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 30px rgba(0, 212, 255, 0.5);
}

.hero-subtitle {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 30px;
  line-height: 1.6;
}

.hero-features {
  display: flex;
  gap: 30px;
  margin-bottom: 40px;
}

.feature {
  display: flex;
  align-items: center;
  gap: 8px;
  color: white;
  font-weight: 500;
}

.feature-icon {
  font-size: 1.5rem;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-card {
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 30px;
  border: 1px solid rgba(0, 212, 255, 0.2);
  max-width: 400px;
  width: 100%;
  transition: all 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .hero-card {
  background: rgba(26, 26, 46, 0.9);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(0, 212, 255, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Светлая тема */
[data-theme="light"] .hero-card {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(0, 212, 255, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.card-header h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 10px;
  transition: color 0.3s ease;
}

.card-header p {
  margin-bottom: 25px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .card-header h3 {
  color: #00d4ff;
}

[data-theme="dark"] .card-header p {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .card-header h3 {
  color: #1a202c;
}

[data-theme="light"] .card-header p {
  color: #4a5568;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 12px;
  font-weight: 500;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact-item {
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .contact-item {
  color: #2d3748;
}

.contact-item .icon {
  font-size: 1.2rem;
  width: 24px;
  text-align: center;
}

@media (max-width: 768px) {
  .hero {
    min-height: 100vh;
    padding-top: 80px;
  }

  .theme-toggle-container {
    top: 120px;
    right: 20px;
  }

  .hero-content {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 60px 0 40px 0;
    text-align: center;
  }

  .hero-title {
    font-size: 2rem;
    line-height: 1.2;
    margin-bottom: 15px;
  }

  .hero-subtitle {
    font-size: 1rem;
    margin-bottom: 25px;
  }

  .hero-features {
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 30px;
  }

  .feature {
    gap: 6px;
    font-size: 0.9rem;
  }

  .feature-icon {
    font-size: 1.2rem;
  }

  .hero-buttons {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 15px;
    width: 100%;
  }

  .hero-buttons .btn {
    width: 100%;
    max-width: 280px;
    margin: 0 auto;
  }

  .hero-card {
    margin: 0 auto;
    padding: 25px;
    max-width: 350px;
  }

  .card-header h3 {
    font-size: 1.3rem;
  }

  .card-header p {
    font-size: 0.9rem;
  }

  .contact-item {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .hero {
    padding-top: 70px;
  }

  .theme-toggle-container {
    top: 110px;
    right: 15px;
  }

  .hero-content {
    padding: 50px 0 30px 0;
  }

  .hero-title {
    font-size: 1.8rem;
  }

  .hero-subtitle {
    font-size: 0.95rem;
  }

  .hero-features {
    gap: 15px;
  }

  .feature {
    font-size: 0.85rem;
  }

  .hero-buttons {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 12px;
    width: 100%;
  }

  .hero-buttons .btn {
    width: 100%;
    max-width: 260px;
    margin: 0 auto;
  }

  .hero-card {
    padding: 20px;
    max-width: 320px;
  }

  .card-header h3 {
    font-size: 1.2rem;
  }

  .contact-item {
    font-size: 0.85rem;
  }
} 