.contact {
  transition: background 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact {
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 100%);
}

/* Светлая тема */
[data-theme="light"] .contact {
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  margin-top: 60px;
}

.contact-info h3 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 30px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact-info h3 {
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .contact-info h3 {
  color: #1a202c;
}

.contact-items {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin-bottom: 40px;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.contact-icon {
  font-size: 1.8rem;
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  box-shadow: 0 10px 20px rgba(0, 212, 255, 0.3);
}

.contact-details h4 {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 5px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact-details h4 {
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .contact-details h4 {
  color: #1a202c;
}

.contact-details a,
.contact-details p {
  text-decoration: none;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact-details a,
[data-theme="dark"] .contact-details p {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .contact-details a,
[data-theme="light"] .contact-details p {
  color: #4a5568;
}

.contact-details a:hover {
  color: #00d4ff;
}

.contact-features {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-features .feature {
  display: flex;
  align-items: center;
  gap: 12px;
  font-weight: 500;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact-features .feature {
  color: #cbd5e0;
}

/* Светлая тема */
[data-theme="light"] .contact-features .feature {
  color: #2d3748;
}

.contact-features .feature-icon {
  font-size: 1.2rem;
  width: 24px;
  text-align: center;
}

.contact-form {
  border-radius: 20px;
  padding: 40px;
  border: 1px solid rgba(0, 212, 255, 0.2);
  position: relative;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .contact-form {
  background: rgba(26, 26, 46, 0.9);
  box-shadow: 
    0 10px 30px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(0, 212, 255, 0.1);
}

/* Светлая тема */
[data-theme="light"] .contact-form {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 
    0 10px 30px rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(0, 212, 255, 0.1);
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  font-weight: 500;
  margin-bottom: 8px;
  transition: color 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .form-group label {
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .form-group label {
  color: #1a202c;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 15px;
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 10px;
  font-size: 16px;
  transition: all 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .form-group input,
[data-theme="dark"] .form-group select,
[data-theme="dark"] .form-group textarea {
  background: rgba(10, 10, 10, 0.8);
  color: #e2e8f0;
}

/* Светлая тема */
[data-theme="light"] .form-group input,
[data-theme="light"] .form-group select,
[data-theme="light"] .form-group textarea {
  background: rgba(255, 255, 255, 0.9);
  color: #2d3748;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #00d4ff;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.2);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-btn {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  font-weight: 600;
  margin-top: 10px;
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.success-message {
  text-align: center;
  padding: 40px 20px;
}

.success-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  display: block;
}

.success-message h3 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #2d3748;
  margin-bottom: 10px;
}

.success-message p {
  color: #718096;
  font-size: 1.1rem;
}

.hidden {
  display: none;
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .contact-form {
    padding: 30px;
  }

  .contact-item {
    gap: 15px;
  }

  .contact-icon {
    width: 40px;
    height: 40px;
    font-size: 1.5rem;
  }
} 