.reviews {
  transition: background 0.3s ease;
}

/* Темная тема */
[data-theme="dark"] .reviews {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
}

/* Светлая тема */
[data-theme="light"] .reviews {
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
}

.reviews-carousel {
  position: relative;
  max-width: 650px;
  margin: 0 auto 40px;
  overflow: visible;
}

.carousel-container {
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  width: 573px;
  height: 800px;
  margin: 0 auto;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.review-slide {
  min-width: 100%;
  flex-shrink: 0;
}

.review-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  overflow: hidden;
  border: 1px solid rgba(0, 212, 255, 0.2);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  width: 573px;
  height: 800px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 20px;
}

/* Темная тема */
[data-theme="dark"] .review-card {
  background: rgba(26, 26, 46, 0.95);
  border: 1px solid rgba(0, 212, 255, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.review-card img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  object-fit: contain;
  transition: transform 0.3s ease;
  border-radius: 20px;
}

.review-card:hover img {
  transform: scale(1.05);
}

/* Кнопки карусели */
.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 212, 255, 0.9);
  border: none;
  color: white;
  font-size: 2rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.carousel-btn:hover {
  background: rgba(0, 212, 255, 1);
  transform: translateY(-50%) scale(1.1);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.4);
}

.prev-btn {
  left: -40px;
}

.next-btn {
  right: -40px;
}

/* Точки навигации */
.carousel-dots {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 30px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: rgba(0, 212, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot:hover {
  background: rgba(0, 212, 255, 0.6);
  transform: scale(1.2);
}

.dot.active {
  background: rgba(0, 212, 255, 1);
  transform: scale(1.3);
}

/* Контролы автопрокрутки */
.carousel-controls {
  display: flex;
  justify-content: center;
}

.control-btn {
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.control-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.4);
}

.control-btn.active {
  background: linear-gradient(135deg, #ff6b6b 0%, #00d4ff 100%);
}

/* Мобильная адаптивность */
@media (max-width: 768px) {
  .reviews-carousel {
    max-width: 450px;
  }

  .carousel-container {
    width: 400px;
    height: 560px;
  }

  .review-card {
    width: 400px;
    height: 560px;
    padding: 15px;
  }

  .carousel-btn {
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
  }

  .prev-btn {
    left: -30px;
  }

  .next-btn {
    right: -30px;
  }

  .control-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .reviews-carousel {
    max-width: 350px;
    margin: 0 auto 30px;
  }

  .carousel-container {
    width: 300px;
    height: 420px;
  }

  .review-card {
    width: 300px;
    height: 420px;
    padding: 10px;
  }

  .carousel-btn {
    width: 45px;
    height: 45px;
    font-size: 1.2rem;
  }

  .prev-btn {
    left: -25px;
  }

  .next-btn {
    right: -25px;
  }

  .dot {
    width: 10px;
    height: 10px;
  }

  .control-btn {
    padding: 8px 16px;
    font-size: 0.85rem;
  }
}

@media (max-width: 360px) {
  .reviews-carousel {
    max-width: 300px;
  }

  .carousel-container {
    width: 250px;
    height: 350px;
  }

  .review-card {
    width: 250px;
    height: 350px;
    padding: 8px;
  }

  .carousel-btn {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }

  .prev-btn {
    left: -20px;
  }

  .next-btn {
    right: -20px;
  }
}

/* Анимации */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.review-card {
  animation: fadeInUp 0.6s ease;
}

/* Эффект загрузки изображений */
.review-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  z-index: 1;
}

.review-card img {
  position: relative;
  z-index: 2;
}

.review-card img[src*="placeholder"] {
  background: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: #ccc;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
} 