.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  backdrop-filter: blur(20px);
  transition: all 0.3s ease;
  padding: 15px 0;
}

/* Темная тема */
[data-theme="dark"] .header {
  background: rgba(10, 10, 10, 0.95);
  border-bottom: 1px solid rgba(0, 212, 255, 0.1);
}

[data-theme="dark"] .header.scrolled {
  background: rgba(10, 10, 10, 0.98);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid rgba(0, 212, 255, 0.2);
  padding: 10px 0;
}

/* Светлая тема */
[data-theme="light"] .header {
  background: rgba(255, 255, 255, 0.95);
  border-bottom: 1px solid rgba(0, 212, 255, 0.1);
}

[data-theme="light"] .header.scrolled {
  background: rgba(255, 255, 255, 0.98);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 212, 255, 0.2);
  padding: 10px 0;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo h2 {
  color: #e2e8f0;
  font-weight: 700;
  margin: 0;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.nav-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 30px;
}

.nav-list button {
  background: none;
  border: none;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
  position: relative;
}

/* Темная тема */
[data-theme="dark"] .nav-list button {
  color: #a0aec0;
}

/* Светлая тема */
[data-theme="light"] .nav-list button {
  color: #4a5568;
}

.nav-list button::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(135deg, #00d4ff 0%, #ff6b6b 100%);
  transition: width 0.3s ease;
}

.nav-list button:hover {
  color: #00d4ff;
}

.nav-list button:hover::after {
  width: 100%;
}

.header-contact .phone {
  color: #00d4ff;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  transition: all 0.3s ease;
  padding: 8px 16px;
  border: 1px solid rgba(0, 212, 255, 0.3);
  border-radius: 8px;
}

.header-contact .phone:hover {
  color: #0a0a0a;
  background: #00d4ff;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
}

.hamburger {
  display: none;
  flex-direction: column;
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
}

.hamburger span {
  width: 25px;
  height: 3px;
  background: #4a5568;
  margin: 3px 0;
  transition: 0.3s;
  border-radius: 2px;
}

.hamburger.open span:nth-child(1) {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.hamburger.open span:nth-child(2) {
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  transform: rotate(45deg) translate(-5px, -6px);
}

@media (max-width: 768px) {
  .nav {
    position: fixed;
    top: 100%;
    left: 0;
    right: 0;
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    backdrop-filter: blur(20px);
  }

  /* Темная тема */
  [data-theme="dark"] .nav {
    background: rgba(10, 10, 10, 0.95);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  }

  /* Светлая тема */
  [data-theme="light"] .nav {
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }

  .nav.open {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }

  .nav-list {
    flex-direction: column;
    padding: 20px;
    gap: 15px;
  }

  .nav-list button {
    font-size: 18px;
    padding: 15px 0;
    width: 100%;
    text-align: left;
    border-bottom: 1px solid rgba(0, 212, 255, 0.1);
  }

  .nav-list button:last-child {
    border-bottom: none;
  }

  .header-contact {
    display: none;
  }

  .hamburger {
    display: flex;
  }

  .hamburger span {
    transition: 0.3s;
  }

  /* Темная тема */
  [data-theme="dark"] .hamburger span {
    background: #e2e8f0;
  }

  /* Светлая тема */
  [data-theme="light"] .hamburger span {
    background: #4a5568;
  }
} 